.hamburger {
	position: absolute;
	top: 15px;
	right: 24px;
	height: 30px;
	width: 40px;
	display: block;
	z-index: 2;
}

.hamburger__inner {
	height: 6px;
	width: 40px;
	background: #fff;
	display: block;
	margin-top: 12px;
	-webkit-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
}

.hamburger__inner::before,
.hamburger__inner::after {
	height: 6px;
	width: 40px;
	background: #fff;
	content: "";
	display: block;
	position: absolute;
	-webkit-transition: all 0.4s ease-in-out;
	transition: all 0.4s ease-in-out;
	z-index: -1;
}

.hamburger__inner::before {
	top: 0;
}

.hamburger__inner::after {
	top: 24px;
}

.hamburger--open .hamburger__inner {
	background: none;
}

.hamburger--open .hamburger__inner::before,
.hamburger--open .hamburger__inner::after {
	top: 12px;
}

.hamburger--open .hamburger__inner::before {
	-webkit-transform: rotate(-45deg);
	transform: rotate(-45deg);
}

.hamburger--open .hamburger__inner::after {
	-webkit-transform: rotate(45deg);
	transform: rotate(45deg);
}

